@import "https://fonts.googleapis.com/css2?family=VT323&display=swap";
html, body {
  background: linear-gradient(#0062ae, #000);
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: VT323, monospace;
  position: fixed;
  overflow: hidden;
}

.sendmessage {
  z-index: 1000;
  width: 100%;
  position: absolute;
  top: 60px;
}

h1 {
  color: #fff;
  text-align: center;
  margin: 20px 0;
  font-family: Open Sans;
}

#welcome {
  color: #e40621;
  text-transform: uppercase;
  font-family: Arial;
  font-size: 28px;
  font-weight: bold;
  display: block;
}

#letitsnow {
  z-index: 1;
  position: absolute;
  inset: 0;
}

.logo {
  text-align: center;
  padding: 40px 0;

  & img {
    width: 300px;
    max-width: 100%;
  }
}

#helpstart {
  z-index: 1000;
  text-align: center;
  padding: 40px;

  & img {
    width: 140px;
    max-width: 100%;
  }
}

[class|="confetti"] {
  position: absolute;
}

#gameover {
  visibility: hidden;
  color: #fff;
  text-align: center;
  background: #000c;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  font-size: 36px;
  display: flex;
  position: absolute;
  top: 0;

  & div {
    position: relative;
    top: -150px;

    & p {
      font-size: 22px;
    }

    & img.playagain {
      width: 200px;
      max-width: 100%;
      margin-top: 30px;
    }

    & img.merryxmas {
      width: 300px;
      max-width: 100%;
    }
  }
}

.icon {
  user-select: none;
  display: inline-block;
}

.icon-offline {
  content: -webkit-image-set(url("100-error-offline.e812e270.png") 1x, url("200-error-offline.47e7e492.png") 2x);
  position: relative;
}

.hidden {
  display: none;
}

.offline .interstitial-wrapper {
  color: #2b2b2b;
  width: 100%;
  max-width: 600px;
  margin: 0 auto;
  padding-top: 100px;
  font-size: 1em;
  line-height: 1.55;
}

.offline .runner-container {
  width: 44px;
  max-width: 600px;
  height: 150px;
  position: absolute;
  top: 35px;
  overflow: hidden;
}

.offline .runner-canvas {
  opacity: 1;
  z-index: 20;
  max-width: 600px;
  height: 150px;
  position: absolute;
  top: 0;
  overflow: hidden;
}

.offline .controller {
  z-index: 10;
  width: 100vw;
  height: 100vh;
  position: absolute;
  top: 0;
  left: 0;
}

#offline-resources {
  display: none;
}

@media (width <= 420px) {
  .suggested-left > #control-buttons, .suggested-right > #control-buttons {
    float: none;
  }

  .snackbar {
    border-radius: 0;
    width: 100%;
    bottom: 0;
    left: 0;
  }
}

@media (height <= 350px) {
  h1 {
    margin: 0 0 15px;
  }

  .icon-offline {
    margin: 0 0 10px;
  }

  .interstitial-wrapper {
    margin-top: 5%;
  }

  .nav-wrapper {
    margin-top: 30px;
  }
}

@media (width >= 600px) and (width <= 736px) and (orientation: landscape) {
  .offline .interstitial-wrapper {
    margin-left: 0;
    margin-right: 0;
  }
}

@media (width >= 420px) and (width <= 736px) and (height >= 240px) and (height <= 420px) and (orientation: landscape) {
  .interstitial-wrapper {
    margin-bottom: 100px;
  }
}

@media (height >= 240px) and (orientation: landscape) {
  .offline .interstitial-wrapper {
    margin-bottom: 90px;
  }

  .icon-offline {
    margin-bottom: 20px;
  }
}

@media (height <= 320px) and (orientation: landscape) {
  .icon-offline {
    margin-bottom: 0;
  }

  .offline .runner-container {
    top: 10px;
  }
}

@media (width <= 240px) {
  .interstitial-wrapper {
    overflow: inherit;
    padding: 0 8px;
  }
}

.arcade-mode, .arcade-mode .runner-container, .arcade-mode .runner-canvas {
  image-rendering: pixelated;
  max-width: 100%;
  overflow: hidden;
}

.arcade-mode #buttons, .arcade-mode #main-content {
  opacity: 0;
  overflow: hidden;
}

.arcade-mode .interstitial-wrapper {
  max-width: 100%;
  height: 100vh;
  overflow: hidden;
}

.arcade-mode .runner-container {
  transform-origin: top;
  z-index: 2;
  margin: auto;
  transition: transform .25s cubic-bezier(.4, 0, 1, 1) .4s;
  left: 0;
  right: 0;
}
